import { Http } from '@angular/http';
import { Injectable } from '@angular/core';

@Injectable()
export class LikeService{

    users: any[];
    constructor(http:Http){
       http.get("https://jsonplaceholder.typicode.com/users")
         .subscribe( response => { 
           this.users = response.json(); 
    });
 
    } 
}
   