import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'orderbutton',
  templateUrl: './orderbutton.component.html',
  styleUrls: ['./orderbutton.component.css']
})
export class OrderbuttonComponent implements OnInit {
  
  isSelected : boolean;
  likeCounter : number;

  constructor() {}

  ngOnInit() {
    this.isSelected = false;
    this.likeCounter = 10;
  }
  
  likeCount(){
      this.isSelected = !this.isSelected;
      this.likeCounter++;
       console.log("event emitted");
  }
}
